import { mapState, mapMutations, mapActions } from 'vuex'
import CommentList from '../commentListTemplate'
import { upyunUrl } from '../../api/config'

export default {
  data () {
    return {
      upyunUrl,
      page: 1,
      commentTotal: 0
    }
  },
  computed: {
    ...mapState([
      'currentMusicItem',
      'commentList'
    ])
  },
  methods: {
    ...mapMutations({
      setCommentList: 'SET_COMMENT_LIST'
    }),
    ...mapActions([
      'getCommentList'
    ]),
    // 显示 - 隐藏, 首页评论弹窗
    onShowHideCommentStateFn () {
      this.$emit('showHideCommentStateChild', false)
    },
    // 获取评论list
    getCommentListFn (isLoading) {
      const data = {
        that: this,
        page: this.page,
        c_id: this.currentMusicItem.id,
        type: 1
      }
      if (isLoading) {
        this.setCommentList([])
        uni.showLoading({ title: 'Loading' })
      }
      this.getCommentList(data).then((res) => {
        this.commentTotal = res.total
        if (isLoading) uni.hideLoading()
      })
    },
    // 上拉加载更多
    scrolltolowerEnd () {
      this.page += 1
      this.getCommentListFn(false)
    }
  },
  props: {
    status: {
      type: Boolean,
      default: false
    },
    vid: {
      type: String,
      default: ''
    }
  },
  components: {
    CommentList
  },
  watch: {
    status (state) {
      if (state) {
        this.page = 1
        this.getCommentListFn(true)
      }
    }
  }
}
